/* Content */
/* ------------------------------------------------------------------------------------------------ */

.sidebar {
  position: -webkit-sticky;
  position: sticky;
  top: 170px;
  background: #FFF;
  padding: 30px 40px;
  border-radius: 20px;
  &__title {
    display: block;
    padding: 5px 10px;
    color: $text;
    font-size: 12px;
    line-height: 18px;
    font-weight: 700; }
  &__menu {
    list-style: none;
    padding-left: 0;
    margin: 0 0 30px;
    &:last-child {
      margin: 0; }
    &> li {
      display: block;
      width: 100%;
      padding: 10px;
      &> a {
        color: $text;
        width: 100%;
        display: flex;
        align-items: center;
        .icon {
          display: block;
          width: 32px;
          height: 32px;
          object-fit: contain;
          object-position: center; }
        span {
          display: block;
          padding-left: 15px; }
        &:hover {
          color: $primary;
          text-decoration: none; } } } }
  @include maxtablet() {
    position: fixed;
    z-index: 200;
    border-radius: 0;
    top: 0;
    left: auto;
    bottom: 0;
    right: 100%;
    width: 100%;
    height: 100%;
    padding: 0;
    transition: all .3s ease-in-out;
    &__toggle {
      position: fixed;
      z-index: 5;
      top: 140px;
      left: -1px;
      width: 40px;
      height: 37px;
      background: #FFF;
      padding: 6px;
      border-radius: 0 4px 4px 0;
      box-shadow: 0px 10px 50px rgba(0, 0, 0, 0.1);
      border: 1px solid $lightblue;
      transition: all .3s ease-in-out;
      span {
        position: absolute;
        top: 8px;
        width: calc(100% - 12px);
        height: 3px;
        background: $secondary;
        border-radius: 2px;
        transition: all .3s ease-in-out;
        &:nth-child(2) {
          top: 16px; }
        &:nth-child(3) {
          top: 24px; } } }
    &__wrap {
      max-height: 100vh;
      overflow: auto;
      padding: 80px 30px 40px; }
    &.open {
      right: 0;
      .sidebar__toggle {
        top: 10px;
        left: 10px;
        border-radius: 4px;
        span {
          transform: rotate(45deg);
          top: 16px;
          border-radius: 0;
          &:nth-child(2) {
            transform: rotate(-45deg); }
          &:nth-child(3) {
            display: none; } } } } } }


.content {
  background: #FFF;
  padding: 30px 40px;
  border-radius: 20px;
  &__header {
    margin: 0 0 50px;
    display: flex;
    flex-wrap: wrap; }
  &__title {
    margin: 0; }
  &__action {
    margin-left: auto;
    .btn + .btn {
      margin-left: 10px; }
    &.full {
      width: 100%;
      max-width: 100%;
      flex: 0 0 100%;
      padding-top: 30px; } }
  &__filter {
    width: 100%;
    max-width: 100%;
    flex: 0 0 100%;
    padding-top: 30px;
    .form-group {
      margin-bottom: 0; } }
  &__table {
    overflow: auto;
    margin-bottom: 50px; }
  &__pagination {
    padding-bottom: 40px; }
  @include maxtablet() {
    padding: 20px 30px;
    &__header {
      flex-direction: column; }
    &__title {
      margin: 0 0 15px; }
    &__action {
      margin-left: 0;
      &.full {
        .btn {
          width: 100%; }
        .btn + .btn {
          margin-left: 0;
          margin-top: 10px; } } }
    &__filter {
      .form-group {
        margin-bottom: 10px; } }
    &__table {
      .table {
        min-width: 1000px; } } } }

.thank-you {
  text-align: center;
  background: #FFF;
  padding: 50px;
  border-radius: 20px;
  .image {
    margin: 0 0 50px; }
  .large {
    font-size: 24px;
    line-height: 44px;
    margin: 0 auto 20px;
    font-weight: 500; }
  &__action {
    padding-top: 10px; }
  @include maxtablet() {
    padding: 50px 30px;
    .image {
      margin: 0 0 20px;
      img {
        height: 80px;
        width: 80px; } }
    .large {
      font-size: 20px;
      line-height: 30px; } } }

