/* ------------------------------------------------------------------------------------------------ */
/* General */
/* ------------------------------------------------------------------------------------------------ */

main {
  background: #EEEEEE;
  min-height: calc(100vh - 200px);
  width: 100%;
  margin-top: 120px;
  padding: 50px 0;
  @include maxtablet() {
    padding: 30px 0;
    margin-top: 84px; } }

.container {
  max-width: 1520px;
  @include mobile {
    max-width: 100%; } }

.row {
  margin-left: -10px;
  margin-right: -10px; }

[class*='col-'] {
  padding-left: 10px;
  padding-right: 10px; }

.cel, .cel-1, .cel-2, .cel-3, .cel-4, .cel-5, .cel-6, .cel-7, .cel-8, .cel-9, .cel-lg-1, .cel-lg-2, .cel-lg-3, .cel-lg-4, .cel-lg-5, .cel-lg-6, .cel-lg-7, .cel-lg-8, .cel-lg-9 {
  position: relative;
  width: 100%;
  padding-right: 10px;
  padding-left: 10px; }

.cel {
  -ms-flex-preferred-size: 0;
  flex-basis: 0;
  -ms-flex-positive: 1;
  flex-grow: 1;
  max-width: 100%; }

.cel-1 {
  -ms-flex: 0 0 10%;
  flex: 0 0 10%;
  max-width: 10%; }

.cel-2 {
  -ms-flex: 0 0 20%;
  flex: 0 0 20%;
  max-width: 20%; }

.cel-3 {
  -ms-flex: 0 0 30%;
  flex: 0 0 30%;
  max-width: 30%; }

.cel-4 {
  -ms-flex: 0 0 40%;
  flex: 0 0 40%;
  max-width: 40%; }

.cel-5 {
  -ms-flex: 0 0 50%;
  flex: 0 0 50%;
  max-width: 50%; }

.cel-6 {
  -ms-flex: 0 0 60%;
  flex: 0 0 60%;
  max-width: 60%; }

.cel-7 {
  -ms-flex: 0 0 70%;
  flex: 0 0 70%;
  max-width: 70%; }

.cel-8 {
  -ms-flex: 0 0 80%;
  flex: 0 0 80%;
  max-width: 80%; }

.cel-9 {
  -ms-flex: 0 0 90%;
  flex: 0 0 90%;
  max-width: 90%; }

@media (min-width: 992px) {
  .cel-lg-1 {
    -ms-flex: 0 0 10%;
    flex: 0 0 10%;
    max-width: 10%; }

  .cel-lg-2 {
    -ms-flex: 0 0 20%;
    flex: 0 0 20%;
    max-width: 20%; }

  .cel-lg-3 {
    -ms-flex: 0 0 30%;
    flex: 0 0 30%;
    max-width: 30%; }

  .cel-lg-4 {
    -ms-flex: 0 0 40%;
    flex: 0 0 40%;
    max-width: 40%; }

  .cel-lg-5 {
    -ms-flex: 0 0 50%;
    flex: 0 0 50%;
    max-width: 50%; }

  .cel-lg-6 {
    -ms-flex: 0 0 60%;
    flex: 0 0 60%;
    max-width: 60%; }

  .cel-lg-7 {
    -ms-flex: 0 0 70%;
    flex: 0 0 70%;
    max-width: 70%; }

  .cel-lg-8 {
    -ms-flex: 0 0 80%;
    flex: 0 0 80%;
    max-width: 80%; }

  .cel-lg-9 {
    -ms-flex: 0 0 90%;
    flex: 0 0 90%;
    max-width: 90%; } }

// BUTTON
.btn {
  display: inline-block;
  font-weight: 500;
  color: $text;
  text-align: center;
  vertical-align: middle;
  cursor: pointer;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  background-color: transparent;
  border: 1px solid transparent;
  padding: 10px 30px;
  font-size: 16px;
  line-height: 24px;
  border-radius: 30px;
  transition: all .3s ease-in-out;
  &:hover {
    color: $text;
    text-decoration: none; }
  &:focus, &.focus {
    outline: 0;
    box-shadow: 0 0 0 0.2rem rgba(0, 123, 255, 0.25); }
  &.disabled, &:disabled {
    opacity: 0.65; }
  &.btn-small {
    font-size: 14px;
    line-height: 20px;
    padding: 5px 16px; }
  &.btn-icon {
    white-space: nowrap;
    .icon {
      display: inline-block;
      margin-right: 15px; }
    span {
      display: inline-block; } } }

.btn-primary {
  color: #FFFFFF;
  background-color: $primary;
  border-color: $primary;
  &:hover {
    color: #FFFFFF;
    background-color: darken($primary, 15%);
    border-color: darken($primary, 15%); }
  &:focus, &.focus {
    color: #FFFFFF;
    background-color: darken($primary, 5%);
    border-color: darken($primary, 5%);
    box-shadow: 0 0 0 0.2rem rgba(38, 143, 255, 0.5); }
  &.disabled, &:disabled {
    pointer-events: none;
    color: #FFFFFF;
    background-color: $border;
    border-color: $border; }
  &:not(:disabled):not(.disabled):active, &:not(:disabled):not(.disabled).active, .show > &.dropdown-toggle {
    color: #FFFFFF;
    background-color: darken($primary, 5%);
    border-color: darken($primary, 5%); }
  &:not(:disabled):not(.disabled):active:focus, &:not(:disabled):not(.disabled).active:focus, .show > &.dropdown-toggle:focus {
    box-shadow: 0 0 0 0.2rem rgba(38, 143, 255, 0.5); } }

.btn-secondary {
  color: #FFFFFF;
  background-color: $secondary;
  border-color: $secondary;
  &:hover {
    color: #FFFFFF;
    background-color: darken($secondary, 5%);
    border-color: darken($secondary, 5%); }
  &:focus, &.focus {
    color: #FFFFFF;
    background-color: darken($secondary, 5%);
    border-color: darken($secondary, 5%);
    box-shadow: 0 0 0 0.2rem rgba(38, 143, 255, 0.5); }
  &.disabled, &:disabled {
    pointer-events: none;
    color: #FFFFFF;
    background-color: $border;
    border-color: $border; }
  &:not(:disabled):not(.disabled):active, &:not(:disabled):not(.disabled).active, .show > &.dropdown-toggle {
    color: #FFFFFF;
    background-color: darken($secondary, 5%);
    border-color: darken($secondary, 5%); }
  &:not(:disabled):not(.disabled):active:focus, &:not(:disabled):not(.disabled).active:focus, .show > &.dropdown-toggle:focus {
    box-shadow: 0 0 0 0.2rem rgba(38, 143, 255, 0.5); } }

.btn-light {
  color: $primary;
  background-color: $lightblue;
  border-color: $lightblue;
  &:hover {
    color: $primary;
    background-color: darken($lightblue, 5%);
    border-color: darken($lightblue, 5%); }
  &:focus, &.focus {
    color: $primary;
    background-color: darken($lightblue, 5%);
    border-color: darken($lightblue, 5%);
    box-shadow: 0 0 0 0.2rem rgba(38, 143, 255, 0.5); }
  &.disabled, &:disabled {
    pointer-events: none;
    color: #FFFFFF;
    background-color: $border;
    border-color: $border; }
  &:not(:disabled):not(.disabled):active, &:not(:disabled):not(.disabled).active, .show > &.dropdown-toggle {
    color: $primary;
    background-color: darken($lightblue, 5%);
    border-color: darken($lightblue, 5%); }
  &:not(:disabled):not(.disabled):active:focus, &:not(:disabled):not(.disabled).active:focus, .show > &.dropdown-toggle:focus {
    box-shadow: 0 0 0 0.2rem rgba(38, 143, 255, 0.5); } }

// PAGINATION
.pagination {
  display: flex;
  justify-content: center;
  list-style: none;
  padding: 0;
  margin: 0 auto;
  .page-link {
    color: $primary;
    text-align: center;
    min-width: 40px;
    display: block;
    font-size: 16px;
    line-height: 20px;
    padding: 10px;
    border-radius: 8px;
    transition: all .3s ease;
    &:hover {
      text-decoration: none;
      font-weight: 700;
      color: $secondary; }
    &.prev {
      margin-right: 40px; }
    &.next {
      margin-left: 40px; } }
  span.page-link {
    pointer-events: none; }
  .page-item.active {
    .page-link {
      color: #FFF;
      background: $primary; } }
  @include maxtablet() {
    .page-link {
      &.prev {
        margin-right: 15px; }
      &.next {
        margin-left: 15px; } }
    .page-item:nth-child(3),
    .page-item:nth-child(4),
    .page-item:nth-child(5),
    .page-item:nth-child(6), {
      display: none; } } }
