/* ------------------------------------------------------------------------------------------------ */
/* Typography */
/* ------------------------------------------------------------------------------------------------ */

p {
  margin: 0 0 20px;
  padding: 0;
  strong,b {
    font-weight: 700; }
  em,i {
    font-style: italic; }
  u {
    text-decoration: underline; }
  a:hover {
    text-decoration: underline; }
  &:last-child {
    margin-bottom: 0; } }

h1, h2, h3, h4 {
  margin: 0 0 20px;
  font-family: "Montserrat Bold", sans-serif;
  font-weight: 700;
  color: $text; }

h1 {
  font-size: 45px;
  line-height: 60px; }

h2 {
  font-size: 30px;
  line-height: 45px; }

h3 {
  font-size: 20px;
  line-height: 45px; }

h4 {
  font-size: 16px;
  line-height: 24px; }

article {
  p {
    word-break: keep-all;
    /* Non standard for webkit */
    -webkit-hyphens: none;
    -moz-hyphens: none;
    -ms-hyphens: none;
    hyphens: none;
    margin: 0 0 20px;
    &:last-child {
      margin: 0; }
    &.upper {
      text-transform: uppercase; } }

  a {
    &:hover {
      text-decoration: underline; } }

  blockquote {
    font-style: italic; }

  li {
    ul, ol {
      margin: 0 20px;
      padding-top: 4px; } }

  ul, ol {
    margin: 0 0 20px;
    padding-left: 24px;
    &:last-child {
      margin: 0; }
    li {
      margin-bottom: 8px;
      &:last-child {
        margin: 0; } } }
  ul {
    list-style-type: disc; }
  ol {
    list-style-type: decimal;
    ol {
      list-style: lower-alpha;
      ol {
        list-style: lower-roman;
        ol {
          list-style: lower-alpha; } } } } }

.text-center,
%text-center {
  text-align: center; }

.text-right,
%text-right {
  text-align: right; }

.text-left,
%text-left {
  text-align: left; }

.text-bold,
%text-bold {
  font-weight: 700; }

.text-semibold,
%text-semibold {
  font-weight: 600; }

.text-regular,
%text-regular {
  font-weight: 400; }

.text-light,
%text-light {
  font-weight: 300; }

.text-capital,
%text-capital {
  text-transform: uppercase; }

.mb {
  &-0 {
    margin-bottom: 0; }
  &-20 {
    margin-bottom: 20px; }
  &-40 {
    margin-bottom: 40px; }
  &-80 {
    margin-bottom: 80px; } }


/* ------------------------------------------------------------------------------------------------ */
/* BASE */
/* ------------------------------------------------------------------------------------------------ */

*, *:after, *:before {
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box; }

img {
  max-width: 100%;
  height: auto; }

hr {
  border-top: 1px solid $border; }

article, aside, figcaption, figure, footer, header, hgroup, main, nav, section {
  display: block; }


html, body {
  font-size: 16px;
  line-height: 24px;
  font-family: 'Roboto', arial, sans-serif;
  font-weight: 400;
  padding: 0;
  margin: 0;
  background-color: #EEEEEE;
  color: $text;
  text-rendering: optimizeLegibility;
  -webkit-font-smoothing: antialiased; }
