/* ------------------------------------------------------------------------------------------------ */
/* Form */
/* ------------------------------------------------------------------------------------------------ */

label {
  color: #333333;
  display: block;
  margin: 0 0 5px; }

.form-control {
  padding: 15px 25px;
  border: 1px solid $border;
  border-radius: 10px;
  display: block;
  width: 100%;
  &:focus {
    border-color: $primary;
    outline: 1px solid lighten($primary, 10%); }
  &[readonly], &.readonly {
    background: #EEEEEE;
    color: #AAAAAA;
    &:focus {
      outline: none;
      border-color: $border; } }
  &[disabled], &.disabled, &:disabled {
    background: #C5C5C5;
    color: #FFFFFF;
    &:focus {
      outline: none;
      border-color: $border; } } }

.form-group {
  margin-bottom: 20px; }

select {
  height: 56px;
  border-radius: 10px;
  background: #D8DAE4;
  border-color: #D8DAE4;
  padding: 15px 20px;
  width: 100%;
  color: #3B4676; }

.select {
  position: relative;
  &.filter {
    &::before {
      content: "";
      z-index: 5;
      display: block;
      width: 32px;
      height: 32px;
      position: absolute;
      top: calc(50% - 16px);
      left: 20px;
      background: url(../images/ic-equalizer.svg) no-repeat;
      background-size: contain;
      background-position: center; }
    .select-control {
      padding-left: 72px; } } }

.select-control {
  background: #D8DAE4;
  border-radius: 10px;
  color: $secondary;
  padding: 15px 20px;
  width: 100%; }

.bootstrap-select > .dropdown-toggle.bs-placeholder, .bootstrap-select > .dropdown-toggle.bs-placeholder:active, .bootstrap-select > .dropdown-toggle.bs-placeholder:focus, .bootstrap-select > .dropdown-toggle.bs-placeholder:hover {
  color: $secondary;
  box-shadow: none; }
.bootstrap-select .dropdown-toggle:focus, .bootstrap-select > select.mobile-device:focus + .dropdown-toggle {
  outline: none !important;
  box-shadow: none; }

.date {
  position: relative;
  &::before {
    content: "";
    z-index: 5;
    display: block;
    width: 32px;
    height: 32px;
    background: url(../images/ic-calendar.svg) no-repeat;
    background-size: contain;
    background-position: center;
    position: absolute;
    top: calc(50% - 16px);
    left: 20px; }
  &::after {
    content: "";
    z-index: 5;
    display: block;
    width: 15px;
    height: 15px;
    background: url(../images/ic-carret-down.svg) no-repeat;
    background-size: contain;
    background-position: center;
    position: absolute;
    top: calc(50% - 7px);
    right: 20px; }
  input {
    position: relative;
    z-index: 2;
    color: $secondary;
    background: #D8DAE4;
    border: 1px solid #D8DAE4;
    width: 100%;
    font-size: 16px;
    line-height: 24px;
    padding: 15px 40px 15px 72px;
    border-radius: 10px;
    &:focus {
      outline: none;
      border: 1px solid #D8DAE4; }
    &::placeholder {
      color: $secondary; } }
  .gj-icon {
    display: none; } }
