/* ------------------------------------------------------------------------------------------------ */
/* Header */
/* ------------------------------------------------------------------------------------------------ */

.header {
  z-index: 200;
  background: #FFF;
  position: fixed;
  width: 100%;
  top: 0;
  left: 0;
  right: 0;
  &__logo {
    display: flex;
    max-width: 160px;
    flex: 0 0 160px;
    img {
      display: block;
      height: 100px;
      width: auto; } }
  &__right {
    display: flex;
    align-items: center;
    justify-content: flex-end;
    max-width: calc(100% - 160px);
    flex: 0 0 calc(100% - 160px); }
  &__notif {
    position: relative;
    .img {
      display: flex;
      justify-content: center;
      align-items: center;
      width: 45px;
      height: 45px;
      background: $primary;
      border-radius: 25px;
      cursor: pointer; } }
  &__profile {
    margin-left: 30px;
    position: relative;
    &::after {
      content: "";
      display: block;
      width: 100%;
      height: 12px;
      position: absolute;
      top: 100%;
      left: 0; }
    .profile {
      display: flex;
      align-items: center;
      position: relative;
      cursor: pointer;
      padding-right: 30px;
      .avatar {
        position: relative;
        border: 4px solid $primary;
        width: 68px;
        height: 68px;
        border-radius: 34px;
        overflow: hidden;
        img {
          display: block; } }
      .name {
        padding-left: 26px;
        font-weight: 700;
        color: $primary;
        max-width: 170px;
        white-space: nowrap;
        overflow: hidden;
        text-overflow: ellipsis; }
      &::after {
        content: "";
        display: block;
        width: 14px;
        height: 14px;
        background: url(../images/ic-chevron.svg) no-repeat;
        background-position: center;
        background-size: contain;
        position: absolute;
        right: 0;
        top: calc(50% - 7px); }
      &:hover {
        text-decoration: none; } }
    &:hover {
      .header__dropdown {
        top: calc(100% + 16px);
        opacity: 1;
        visibility: visible; } } }
  &__dropdown {
    list-style: none;
    position: absolute;
    top: 100%;
    left: 0;
    width: 100%;
    padding: 30px;
    background: #FFF;
    border-radius: 30px;
    box-shadow: 0px 10px 50px rgba(0, 0, 0, 0.1);
    opacity: 0;
    visibility: hidden;
    transition: all .3s ease-in-out;
    &> li {
      position: relative;
      padding: 10px;
      .icon {
        position: relative;
        width: 32px;
        height: 32px;
        object-fit: contain;
        object-position: center; }
      span {
        display: block;
        padding-left: 16px; }
      &> a {
        display: flex;
        align-items: center;
        color: $text;
        &:hover {
          color: $primary;
          text-decoration: none; } } } }
  &__wrap {
    display: flex;
    align-items: center;
    padding-top: 10px;
    padding-bottom: 10px; }

  @include maxtablet() {
    &__logo {
      img {
        height: 64px; } }
    &__notif {
      .img {
        height: 32px;
        width: 32px;
        img {
          height: 20px;
          width: auto; } } }
    &__profile {
      margin-left: 15px;
      .profile {
        padding-right: 0;
        .avatar {
          height: 48px;
          width: 48px; }
        .name {
          display: none; }
        &::after {
          display: none; } } }
    &__dropdown {
      width: 300px;
      left: auto;
      right: 0; } } }

