.header__wrap { display: flex; align-items: center; padding-top: 10px; padding-bottom: 10px; }

@media (max-width: 991px) { .header__logo img { height: 64px; }
  .header__notif .img { height: 32px; width: 32px; }
  .header__notif .img img { height: 20px; width: auto; }
  .header__profile { margin-left: 15px; }
  .header__profile .profile { padding-right: 0; }
  .header__profile .profile .avatar { height: 48px; width: 48px; }
  .header__profile .profile .name { display: none; }
  .header__profile .profile::after { display: none; }
  .header__dropdown { width: 300px; left: auto; right: 0; } }

/* ------------------------------------------------------------------------------------------------ */
/* Footer */
/* ------------------------------------------------------------------------------------------------ */
.footer { background: #3B4676; color: #fff; }

.footer__wrap { padding-top: 28px; padding-bottom: 28px; }

/* ------------------------------------------------------------------------------------------------ */
/* Content */
/* ------------------------------------------------------------------------------------------------ */
.sidebar { position: -webkit-sticky; position: sticky; top: 170px; background: #FFF; padding: 30px 40px; border-radius: 20px; }

.sidebar__title { display: block; padding: 5px 10px; color: #333333; font-size: 12px; line-height: 18px; font-weight: 700; }

.sidebar__menu { list-style: none; padding-left: 0; margin: 0 0 30px; }

.sidebar__menu:last-child { margin: 0; }

.sidebar__menu > li { display: block; width: 100%; padding: 10px; }

.sidebar__menu > li > a { color: #333333; width: 100%; display: flex; align-items: center; }

.sidebar__menu > li > a .icon { display: block; width: 32px; height: 32px; object-fit: contain; object-position: center; }

.sidebar__menu > li > a span { display: block; padding-left: 15px; }

.sidebar__menu > li > a:hover { color: #0083CB; text-decoration: none; }

@media (max-width: 991px) { .sidebar { position: fixed; z-index: 200; border-radius: 0; top: 0; left: auto; bottom: 0; right: 100%; width: 100%; height: 100%; padding: 0; transition: all .3s ease-in-out; }
  .sidebar__toggle { position: fixed; z-index: 5; top: 140px; left: -1px; width: 40px; height: 37px; background: #FFF; padding: 6px; border-radius: 0 4px 4px 0; box-shadow: 0px 10px 50px rgba(0, 0, 0, 0.1); border: 1px solid #CCE6F5; transition: all .3s ease-in-out; }
  .sidebar__toggle span { position: absolute; top: 8px; width: calc(100% - 12px); height: 3px; background: #3B4676; border-radius: 2px; transition: all .3s ease-in-out; }
  .sidebar__toggle span:nth-child(2) { top: 16px; }
  .sidebar__toggle span:nth-child(3) { top: 24px; }
  .sidebar__wrap { max-height: 100vh; overflow: auto; padding: 80px 30px 40px; }
  .sidebar.open { right: 0; }
  .sidebar.open .sidebar__toggle { top: 10px; left: 10px; border-radius: 4px; }
  .sidebar.open .sidebar__toggle span { transform: rotate(45deg); top: 16px; border-radius: 0; }
  .sidebar.open .sidebar__toggle span:nth-child(2) { transform: rotate(-45deg); }
  .sidebar.open .sidebar__toggle span:nth-child(3) { display: none; } }

.content { background: #FFF; padding: 30px 40px; border-radius: 20px; }

.content__header { margin: 0 0 50px; display: flex; flex-wrap: wrap; }

.content__title { margin: 0; }

.content__action { margin-left: auto; }

.content__action .btn + .btn { margin-left: 10px; }

.content__action.full { width: 100%; max-width: 100%; flex: 0 0 100%; padding-top: 30px; }

.content__filter { width: 100%; max-width: 100%; flex: 0 0 100%; padding-top: 30px; }

.content__filter .form-group { margin-bottom: 0; }

.content__table { overflow: auto; margin-bottom: 50px; }

.content__pagination { padding-bottom: 40px; }

@media (max-width: 991px) { .content { padding: 20px 30px; }
  .content__header { flex-direction: column; }
  .content__title { margin: 0 0 15px; }
  .content__action { margin-left: 0; }
  .content__action.full .btn { width: 100%; }
  .content__action.full .btn + .btn { margin-left: 0; margin-top: 10px; }
  .content__filter .form-group { margin-bottom: 10px; }
  .content__table .table { min-width: 1000px; } }

.thank-you { text-align: center; background: #FFF; padding: 50px; border-radius: 20px; }

.thank-you .image { margin: 0 0 50px; }

.thank-you .large { font-size: 24px; line-height: 44px; margin: 0 auto 20px; font-weight: 500; }

.thank-you__action { padding-top: 10px; }

@media (max-width: 991px) { .thank-you { padding: 50px 30px; }
  .thank-you .image { margin: 0 0 20px; }
  .thank-you .image img { height: 80px; width: 80px; }
  .thank-you .large { font-size: 20px; line-height: 30px; } }
